<template>
  <div>
    <!--  -->
    <h3 class="title">{{ photo.title }}</h3>
    <!--  -->
    <div class="subtitle">
      <span>{{ photo.publishTime | dataFormat('YYYY-MM-DD HH:mm:ss') }}</span>
      <span>{{ photo.click }}</span>
    </div>

    <hr>

    <!--  -->
    <div>
      <!--  -->
      <vue-preview
        :list="list"
        :thumbImageStyle="{width: '30%', margin: '1%'}"
        :previewBoxStyle="{border: '1px solid #eee'}"
        :tapToClose="true"
        @close="closeHandler"
        @destroy="destroyHandler"
      />
    </div>
    
    <!--  -->
    <comment-box :id="id"></comment-box>
  </div>
</template>

<script>
import Comment from '../subcomponents/Comments.vue'

export default {
  data() {
    return {
        // 
        photo: {},
        // 
        list: []
    }
  },
  created() {
    this.getPhotoInfoById()
  },
  methods: {
    getPhotoInfoById(){
      console.log(this.id)
      this.$axios({
        method: 'get',
        url: '/getphotoinfo/' + this.id
      }).then(res => {
        console.log('', res.data)
        if (res.status === 200) {
          this.photo = res.data
          res.data.img_urls.forEach(item => {
            item.w = 500
            item.h = 322
          })
          this.list = res.data.img_urls
        }
      }).catch(err => {
        console.log('')
      })
    },
    // 
    closeHandler() {
      console.log('closeHandler')
    },
    // 
    destroyHandler() {
      console.log('destroyHandler')
    }
  },
  props: ['id'],
  components: {
    'comment-box': Comment
  }
}
</script>

